@import '~/src/assets/styles/variables';

.root {
  @media @desktop {
    width: var(--card-width);
  }

  .avatar {
    @media @desktop {
      width: var(--card-width);
      height: var(--card-width);
    }
  }

  .meta {
    dl {
      display: flex;
      flex-wrap: wrap;
      margin: 0;
    }

    dt {
      width: 1.5rem;
    }

    dd {
      width: calc(100% - 1.5rem);

      &:last-child {
        margin: 0;
      }
    }
  }

  .favor {
    display: block;
    width: 100%;
    height: 100%;
    color: #f00;
    opacity: 0.7;

    &:hover {
      opacity: 1;
    }
  }
}
