/* stylelint-disable at-rule-no-unknown */
:root {
  /* Layout
   * ======================================================================== */
  --layout-width: 1156px;

  /* Card
   * ======================================================================== */
  --card-width: 269px;
  --card-height: 478px; /* height of 1 card */
}

@screen-desktop: 768px;
@desktop:   ~"only screen and (min-width: @{screen-desktop})";

@screen-mobile: (@screen-desktop - 1px);
@mobile:   ~"only screen and (max-width: @{screen-mobile})";
